.profile {
    margin-bottom: 70px;
  }
  
  .profile-card {
    width: 100%;
    height: 360px;
    position: relative;
    margin-top: 60px;
  }
  
  .profile-details {
    position: absolute;
    z-index: 1;
    right: 155px;
    text-align: left;
  }
  
  .profile-photo {
    position: absolute;
    width: 295px;
    height: 412px;
    left: 133px;
  }
  
  .profile-name {
    font-size: 60px;
    color: white;
    text-shadow: 0 3px 6px #90000036;
  }
  
  .profile-occupation {
    font-size: 29px;
    color: white;
    text-shadow: 0 3px 6px #5d000029;
    padding-bottom: 20px;
  }
  
  .profile-rate {
    padding-top: 20px;
    font-size: 24px;
    font-weight: 600;
  }
  
  .profile-location {
    padding: 20px;
    margin-top: 50px;
    font-size: 14px;
  }
  
  .profile-exp {
    font-size: 14px;
    color: #b9b9b9;
    padding: 20px;
  }
  .profile-desc {
    padding: 20px;
    font-size: 12px;
    color: #525252;
  }
  
  .profile-about {
    font-size: 14px;
    padding-bottom: 0;
    margin-bottom: 0;
  }
  
  .profile-carousel {
    cursor: grab;
    overflow: hidden;
    padding-left: 20px;
  }
  
  .profile-slider {
    width: 100%;
    display: flex;
    gap: 20px;
  }
  
  .slider-item img {
    pointer-events: none;
  }
  
  .profile-hire {
    width: 428px;
    position: fixed;
    display: flex;
    align-items: stretch;
    justify-content: center;
    height: 66px;
    box-shadow: 0px -3px 6px #00000040;
    background-color: white;
    z-index: 1;
    bottom: 0;
  }
  
  .hire-btn {
    width: 100%;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: #ff7575;
    cursor: pointer;
    margin: 10px;
    font-size: 16px;
  }
  