.main-card {
    display: flex;
    height: 100px;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 15px;
    padding: 10px 0;
    cursor: pointer;
  }
  
  .photo-name {
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    width: 70%;
    color: #525252;
  }
  
  .name {
    font-size: 20px;
    font-weight: 600;
  }
  
  .location {
    font-size: 10px;
    font-weight: 600;
  }
  
  .occupation {
    font-size: 10px;
    font-weight: 600;
    margin-top: 5px;
  }
  
  .cards {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 0 10px;
    padding-left: 0;
  }
  
  .rating {
    display: flex;
    gap: 5px;
    margin-top: 5px;
  }
  
  .rate {
    font-size: 24px;
    text-align: center;
    font-weight: 600;
  }
  